"use client";

import React from "react";

const Dashboard = () => {
  return (
    <div className="flex flex-1">
      <div className="flex h-full w-full flex-1 flex-col gap-4 rounded-tl-2xl border border-neutral-200 bg-white p-6 md:p-10 dark:border-neutral-700 dark:bg-neutral-900">
        
        {/* Hàng 1: Thống kê nhanh */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          {["Users", "Orders", "Revenue", "Growth"].map((title, idx) => (
            <div
              key={idx}
              className="rounded-xl bg-gray-100 dark:bg-neutral-800 p-4 flex flex-col items-center justify-center shadow-sm hover:shadow-md transition"
            >
              <span className="text-sm text-gray-500">{title}</span>
              <span className="text-xl font-semibold mt-2">--</span>
            </div>
          ))}
        </div>

        {/* Hàng 2: Biểu đồ / bảng */}
        <div className="flex flex-1 gap-4">
          <div className="w-2/3 rounded-xl bg-gray-100 dark:bg-neutral-800 p-4 shadow-sm"></div>
          <div className="flex-1 rounded-xl bg-gray-100 dark:bg-neutral-800 p-4 shadow-sm"></div>
        </div>

      </div>
    </div>
  );
};

export default Dashboard;
